.HexagonsWrapper {
  position: fixed;
  inset: 0;
  z-index: -1;
}
.HexagonsWrapper img {
  position: absolute;
}
.HexagonsWrapper img:nth-child(1) {
  left: 0;
  transform: translate(-50%, -30%);
  opacity: 0.2;
}
.HexagonsWrapper img:nth-child(2) {
  right: 0;
  transform: translate(50%, 30%);
  width: 50vw;
  opacity: 0.3;
}
.HexagonsWrapper img:nth-child(3) {
  bottom: 1%;
  left: 1%;
  width: 20vw;
  opacity: 0.1;
}