.LoginBlock {
  position: absolute;
  min-height: 40vh;
  width: 85vw;
  background-color: #f7f7f7;
  border-radius: 15px;
  border: 1px solid #808080;
  padding: 10px 30px 20px 30px;
  top: 40%;
  right: 50%;
  transform: translate(50%, -50%);
}
.LoginBlock .Title {
  margin: 10px 0;
  font-size: 1.5rem;
}
.LoginBlock .SubmitButtonWrapper {
  margin-top: 30px;
  text-align: center;
}

@media screen and (min-width: 769px) {
  .LoginBlock {
    width: 50%;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
  }
}